<template>
  <v-card class="pa-4 mx-auto" id="card-container">
    <template #title>
      <div class="d-flex py-2">
        <h2 class="text-h4 font-weight-bold">
          The White Order Lineage 2 Users
        </h2>
      </div>
      <hr />
    </template>
    <template v-slot:text>
      <div class="search-container">
        <v-text-field
          v-model="search"
          label="Search"
          variant="underlined"
          hide-details
          single-line
          class="search-field"
        >
          <template #prepend-inner>
            <v-icon color="#1f9ee5">mdi-magnify</v-icon>
          </template>
        </v-text-field>
      </div>
    </template>

    <v-data-table
      hover
      :headers="headers"
      :items="users"
      :search="search"
      fixed-header
      class="table-container"
      density="comfortable"
    >
      <!-- Using CSS-based grouping instead of template override to preserve sorting -->
      <template #[`item.user`]="{ item }">
        <div class="d-flex align-center justify-center">
          <v-icon color="#1f9ee5" class="me-2">mdi-account</v-icon>
          {{ item.user }}
        </div>
      </template>
      <template #[`item.class`]="{ item }">
        <div class="text-center">
          {{ item.class || "-" }}
        </div>
      </template>
      <template #[`item.level`]="{ item }">
        <div class="text-center">
          {{ item.level || "-" }}
        </div>
      </template>
      <template #[`item.actions`]="{ item }">
        <v-btn
          size="small"
          icon
          variant="text"
          color="#1f9ee5"
          @click="openModal(item)"
        >
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
      </template>
    </v-data-table>

    <DkpModal v-model="dialog" :item="selectedItem" @saved="handleUserSaved" />
  </v-card>
</template>
<script setup>
import { ref, computed, defineProps, defineEmits } from "vue";
import DkpModal from "./DkpModal.vue";
//eslint-disable-next-line no-unused-vars
const props = defineProps({
  users: {
    type: Array,
    default: () => [],
  },
  events: {
    type: Object,
    default: () => ({}),
  },
  eventTypes: {
    type: Array,
    default: () => [],
  },
  attendanceData: {
    type: Object,
    default: () => ({}),
  },
});

const emit = defineEmits(["user-updated"]);

const search = ref("");
const dialog = ref(false);
const selectedItem = ref(null);

const openModal = (item) => {
  selectedItem.value = item;
  dialog.value = true;
};

const eventTypesToDisplay = [
  "Field Raids",
  "Epics",
  "Clan Hall/Fort",
  "Call To Arms",
  "Siege",
];

const headers = computed(() => {
  const baseHeaders = [
    {
      align: "center",
      key: "user",
      sortable: true,
      title: "User Name",
      width: "150px",
    },
    {
      key: "class",
      align: "center",
      title: "Class",
      sortable: true,
      width: "120px",
    },
    {
      key: "level",
      align: "center",
      title: "Level",
      sortable: true,
      width: "80px",
    },
    {
      key: "dkp",
      align: "center",
      title: "DKP",
      width: "100px",
      sortable: true,
    },
  ];
  // Add headers for each event type
  const eventHeaders = eventTypesToDisplay.map((type) => ({
    key: `event_${type}`,
    align: "center",
    title: type,
    sortable: true,
    width: "100px",
  }));

  return [
    ...baseHeaders,
    ...eventHeaders,
    {
      key: "actions",
      align: "center",
      title: "Actions",
      width: "80px",
    },
  ];
});

const handleUserSaved = () => {
  emit("user-updated");
};
</script>

